import React, { Component } from 'react';
import style from './../style/Main_component.css';

class MainComponents extends Component {
	render() {
		return(
			<div className="main__component component">
				<img className="component__img" src={require('./../img/' + this.props.image)} alt="image"/>
				<div className={"component__info info " + this.props.bg}>
					<h3 className={"info__title " + this.props.textColor}>{this.props.title}</h3>
					<p className="info__text">{this.props.text}</p>
					<p className={"info__date " + this.props.textColor}>{this.props.date}</p>
				</div>
			</div>
		);
	}
}

export default MainComponents