import React, { Component } from 'react';
import style from './../style/AbUs_component.css';

class AbUsComponent extends Component {
	render() {
		return(
			<div className="AbUs_component component">
				<h3 className="component__title">{this.props.title}</h3>
				<p className="component__text">{this.props.text}</p>
			</div>
		);
	}
} 

export default AbUsComponent