import React, { Component } from 'react';
import style from './../style/Contact.css';

class Contact extends Component {
	render() {
		return (
			<div className="contactUs">
				<div className="contactUs__location location">
					<h3 className="location__title">Our Location</h3>
					<iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2565.1679257859882!2d36.231025006233374!3d49.98946105631396!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4127a0f114b4c381%3A0xdcd64ec37c54e0f0!2z0L_Quy4g0JrQvtC90YHRgtC40YLRg9GG0LjQuCwgMiwg0KXQsNGA0YzQutC-0LIsINCl0LDRgNGM0LrQvtCy0YHQutCw0Y8g0L7QsdC70LDRgdGC0YwsIDYxMDAw!5e0!3m2!1sru!2sua!4v1555425564485!5m2!1sru!2sua" frameborder="0" allowfullscreen></iframe>
				</div>
				<div className="contactUs__form">
					<h3 className="form__title">Contact Us</h3>
					<form action="#" className="form">
						<input type="text" className="form__name" placeholder="Name..."/>
						<input type="email" className="form__email" placeholder="Email..."/>
						<textarea name="Message" className="form__text" placeholder="You Message..."></textarea>
						<input className="form__btn" type="button" value="Send It"/>
					</form>
				</div>
			</div>
		);
	}
}

export default Contact